import wPicker from '@/components/w-picker/w-picker.vue';
export default {
	components: {
		wPicker
	},
	data() {
		return {
			childListProps: { label: 'nickname', value: 'zid' },
			childList: [
				{
					zid: '',
					nickname: '当前账号'
				}
			],
			defaultDate: '',
			startyear: ''
		};
	},
	methods: {
		deliveryPage() {
			uni.navigateTo({
				url: '/pages/shopcart/delivery'
			});
		},
		// 显示子账号
		selectAccount() {
			this.$refs.account.show();
		},
		onConfirmAccount(e) {
			console.log(e);
		},
		// 显示配送日期
		selectDate() {
			this.$refs.date.show();
		},
		onConfirmDate(e) {
			let { year, month, day } = e.obj;
			this.defaultDate = `${year}-${month}-${day}`;
		},
		submit() {}
	},
	onShow() {},
	onReady() {
		var date = new Date();
		let n = date.getTime() + 24 * 3600000;
		date.setTime(n);
		var year = date.getFullYear();
		var month = date.getMonth() + 1;
		var day = date.getDate();
		this.startyear = year;
		if (month < 10) {
			month = '0' + month;
		}

		if (day < 10) {
			day = '0' + day;
		}
		this.defaultDate = year + '-' + month + '-' + day;
	}
};
